
import { defineComponent } from "vue"

const CLICK_EVENT_NAME = "click"

export default defineComponent({
  name: "RegularButton",
  emits: [CLICK_EVENT_NAME],
  components: {},
  
  props: {
    disabled: Boolean,
    large: Boolean,
  },

  methods: {
    onClick() {
      if (this.disabled) {
        return
      }

      this.$emit(CLICK_EVENT_NAME)
    }
  }
})
