
import { defineComponent } from "vue"
import {
  INPUT_EVENT_NAME,
  PASSWORD_INPUT_TYPE,
  TEXT_INPUT_TYPE,
} from "./constants"

export default defineComponent({
  name: "RegularField",
  components: {},

  emits: [
    INPUT_EVENT_NAME,
  ],
  
  props: {
    placeholder: String,
    error: String,
    large: Boolean,
    notShow: Boolean,
    maxlength: {
      type: Number,
      default: 255,
    },
    type: {
      type: String,
      default: TEXT_INPUT_TYPE,
    }
  },

  data () {
    return {
      value: "",
      visible: false,
    }
  },

  computed: {
    hasError(): boolean {
      return this.error != undefined && this.error.length > 0
    },

    showButton(): boolean {
      return !this.notShow && this.type == PASSWORD_INPUT_TYPE && this.value.length > 0
    },

    inputType(): string {
      if (this.type === PASSWORD_INPUT_TYPE && !this.visible) {
        return PASSWORD_INPUT_TYPE
      } else {
        return TEXT_INPUT_TYPE
      }
    },
  },

  methods: { 
    focus() {
      let entryRef: any = this.$refs.entry
      entryRef.focus()
    },

    switchVisibility() {
      this.visible = !this.visible
    },

    onChange() {
      this.$emit(INPUT_EVENT_NAME, this.value)
    }
  },

})
